@import "../../styles/variables";

.Container {
  display: flex;
  gap: 1rem;
  opacity: 0;
  .data {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    color: $primary;
    gap: 0.25rem;
    div {
      font-size: $m-font-size;
      padding: 1rem;
      background: rgb(100,100,100);
      span {
        background: $highlight;
        font-size: $xs-font-size;
        line-height: $xs-font-size * 1.5;
        border: $highlight 1px solid;
        border-radius: 3rem;
        box-shadow: $shadow 3px 3px 2px;
        text-shadow: $shadow 1px 1px 1px;
        padding: 0 .5rem;
        float: right;
      }
    }
  }

  &-enter-active {
    opacity: 1;
    transition: opacity 500ms;
  }
  &-enter-done {
    opacity: 1;
  }
}